<template>
  <!-- 签约居民信息管理 -->
  <div class="list-common-box sign-resident-box">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb :isLevel='2'></Breadcrumb>
    </div>
    <!-- 主要数据 -->
    <div class="card-box">
      <!-- 搜索 -->
      <div class="search-bar">
        <!-- 左 -->
        <div class="search-left">
          <div class="search-item">
            <span class="search-label">身份证号</span>
            <el-input v-model.trim="searchKey.idNo" placeholder="请输入身份证号" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">姓名</span>
            <el-input v-model.trim="searchKey.userName" placeholder="请输入姓名" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">性别</span>
            <el-select placeholder="请选择性别" v-model="searchKey.sexType" clearable>
              <el-option v-for="item in sexOption" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="search-item">
            <span class="search-label">是否解约</span>
            <el-select placeholder="请选择" v-model="searchKey.terminateStatus" clearable>
              <el-option v-for="item in terminateStatusOption" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="search-right">
          <el-button type="primary" plain icon="el-icon-search" @click="querylist()">查询</el-button>
          <el-button type="primary" plain icon="el-icon-delete" @click="Refreshlist()">清空</el-button>
        </div>
      </div>
      <!-- 表格 -->
      <div class="table-box">
        <el-table :data="dataList" tooltip-effect="dark" stripe height="100%"> 
          <el-table-column label="序号" align="center" width="80">
            <template slot-scope="scope">
              {{scope.$index+(pageNum - 1) * pageSize + 1}}
            </template>
          </el-table-column>
          <el-table-column prop="userName" label="姓名" align="center" width="120">
            <template slot-scope="scope">
              {{scope.row.userName?scope.row.userName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="idNo" label="身份证号" align="center" width="180">
            <template slot-scope="scope">
              {{scope.row.idNo?scope.row.idNo:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="mobile" label="电话" align="center">
            <template slot-scope="scope">
              {{scope.row.mobile?scope.row.mobile:EMPTYTEXT}}
            </template>
          </el-table-column>
           <el-table-column prop="sexTypeStr" label="性别" align="center" width="80">
            <template slot-scope="scope">
              {{scope.row.sexTypeStr?scope.row.sexTypeStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="personTypeStr" label="居民类型" align="center">
            <template slot-scope="scope">
              {{scope.row.personTypeStr?scope.row.personTypeStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="servicePackageName" label="服务包" align="center">
            <template slot-scope="scope">
              {{scope.row.servicePackageName?scope.row.servicePackageName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="signDateStr" label="签约时间"  align="center" width="170">
            <template slot-scope="scope">
              {{scope.row.signDateStr?scope.row.signDateStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="invalidDateStr" label="到期时间"  align="center" width="170">
            <template slot-scope="scope">
              {{scope.row.invalidDateStr?scope.row.invalidDateStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="terminateStatusStr" label="是否解约"  align="center" width="120">
            <template slot-scope="scope">
              {{scope.row.terminateStatusStr?scope.row.terminateStatusStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="registerUser" label="创建人"  align="center" width="120">
            <template slot-scope="scope">
              {{scope.row.registerUser?scope.row.registerUser:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column label="操作"  align="center" width="100">
            <template slot-scope="scope">
              <el-button type="primary" @click="handleView(scope.row)" class="green">查看详情</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页 -->
      <div class="page-box">
        <Paginaction class='pagination' :goPage="pageNum" :totalNum="totalNum" @updateList="changePage" ref="paginationEvent"/>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'sign-resident',
    data() {
      return {
        searchKey:{
          idNo:'',//身份证号
          userName:'', //姓名
          sexType:'',//性别
          terminateStatus:''//是否解约
        },
        sexOption:[
          {
            value: 0,
            label: '全部'
          },
          {
            value: 1,
            label: '男'
          },
          {
            value: 2,
            label: '女'
          }
        ],
        terminateStatusOption: [
          {
            value: 0,
            label: '全部'
          },
          {
            value: 1,
            label: '是'
          },
          {
            value: 2,
            label: '否'
          }
        ], //是否解约下拉数据
        pageNum:1,//当前页
        pageSize:10,//每页条数
        totalNum: 0, //总条数
        dataList: [] //签约居民信息列表
      }
    },
    created() {
      this.getDatalist() //获取签约居民信息列表
    },
    methods: {
      //查看详情
      handleView(scope) {
        this.$router.push({
          path: '/resident-manage/sign-resident/detail?id=' + scope.serviceId
        })
      },
      //查询
      querylist() {
        this.pageNum = 1
        this.getDatalist()
      },
      //重置
      Refreshlist() {
        this.pageNum = 1
        this.pageSize = 10
        Object.keys(this.searchKey).forEach(k => this.searchKey[k] = '')
        this.getDatalist()
      },
      //分页改变
      changePage(query){
        this.pageNum = query.pageNum
        this.pageSize = query.pageSize
        this.getDatalist()
      },
      //获取签约居民信息列表
      getDatalist(query) {
        this.$emit("viewLoad", true);
        let params={}
        params.pageNum  = this.pageNum
        params.pageSize = this.pageSize
        params = Object.assign({},params,this.searchKey)
        this.$http.post(process.env.VUE_APP_API + '/family/service/getDtoByPage', params).then(res => {
            if (res.code===200) {
              this.dataList = res.data.list || [] //签约居民信息
              this.totalNum = res.data.total //总条数
              this.pageNum = res.data.pageNum //当前页
              this.pageSize = res.data.pageSize //每页条数
            } else {
              this.$message.error(res.msg);
            }
            this.$emit("viewLoad", false);
          })
          .catch(error => {
            this.$emit("viewLoad", false);
            this.$message.error(error);
          });
      }
    }
  }
</script>
<style lang='less' scoped>
</style>